.welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;
  text-align: center;
  font-size: 30px;
  font-weight: 500;
}

.link {
  height: 50px;
  padding: 0 20px;
  background-color: #081c24;
  color: #fff;
  border-radius: 5px;
  margin-top: 20px;

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
}
