.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;

    button {
        height: 50px;
        padding: 0 20px;
        margin:  0 20px;
        border: 1px solid #ccc;
        border-radius: 3px;

        &:hover {
            background-color: #878787;
        }
    }
}