$main_color:#081c24;
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap&subset=cyrillic');
* {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

html,
body {
	min-height: 100%;
}
body {
	display: flex; 
	flex-direction: column;
	min-height: 100vh;
	font-family: 'Roboto', sans-serif;
	font-size: 18px;
	line-height: normal;
	margin: 0;	
	color: $main_color; 
}
main {
	flex-grow: 1;
}


a {
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
p,
ul {
	margin: 0;
	padding: 0;
	font-weight: 400;
}

li {
	list-style: none;
}

input {
	outline: none;  

	&:required {
		box-shadow: none;
	}
}
input[type="submit"] {
	font-family: 'Roboto', sans-serif;
	cursor: pointer;
}

button {
	cursor: pointer; 
	border: 0; 
	padding: 0;  
	font-weight: 400;	
	font-size: 18px;
	transition: 0.2s ease-out;
	background: none;
	outline: none;

	&:disabled {
		cursor: auto;
	}
}
.btn {
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Roboto', sans-serif;
	cursor: pointer; 
	border: 0; 
	padding: 0;  
	font-weight: 400;	
	font-size: 16px;
	transition: 0.2s ease-out;
	background: none;
	outline: none;

	&:disabled {
		cursor: auto;
	}
}

img {
	display: block;
}

.bold {
	font-weight: 700;
}

.wr {
	width: 100%;
	max-width: 1210px;
	margin: 0 auto;
	padding: 0 20px;
}

.no_scroll {
	overflow: hidden;
}

.input {
	font-family: 'Roboto', sans-serif;
	border: 1px solid #ccc;
}